.title {
	display: inline-block;
	width: 100%;
	font-family: 'Poppins';
	font-size: 64px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	margin: 0 auto;
	margin-top: 32px;
	margin-bottom: 24px;
	color: rgb(147, 25, 25);
	
}

.item {
	display: flex;
	color: white; 
	align-items: center;
	justify-content: space-between;
	padding: 16px;
	background-color: #00000000;
	// margin-bottom: 16px;
	// border-radius: 4px;
	font-weight: 500;
	border-bottom: 2px solid white;
	&:last-child {
		margin-bottom: 0;
	}
}
.todoDetails {
	display: flex;
	align-items: center;
	gap: 16px;
}

.texts {
	display: flex;
	flex-direction: column;
	overflow: hidden;
	color: white;
}
.todoText {
	word-break: break-all;
	font-weight: 500;
	font-size: 18px;
	color: white;
	margin-bottom: 5px;
}

.completed {
	text-decoration: line-through;
	opacity: 0.7;
}
.time {
	display: block;
	font-size: 15px;
	font-weight: 300;
	margin-top: -0.2rem;
	color: var(--black-2);
}
.todoActions {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 1rem;
}
.icon {
	font-size: 25px;
	padding: 0.5rem;
	border-radius: 4px;
	background-color: var(--gray-1);
	color: var(--black-2);
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: 0.3s ease background-color;
}