* {
  margin: 0;
}

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  background-color: #d6d4ce;
  max-width: 450px;
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 32px;
  border-radius: 8px;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.closeButton {
  position: absolute;
  top: 70px;
  right: 20px;
  transform: translateY(-100%);
  font-size: 40px;
  padding: 8px;
  border-radius: 4px;
  font-weight: 900;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.3s ease all;
  z-index: 1;
}
.closeButton:hover {
  color: rgb(228, 50, 50);
  font-weight: 900;
}

.formTitle {
  color: black;
  font-size: 32px;
  font-weight: 600;
  margin-bottom: 32px;
  text-transform: capitalize;
}

.form {
  width: 100%;
}
.form label {
  font-size: 18px;
  color: black;
}
.form label input,
.form label select {
  margin-top: 8px;
  margin-bottom: 16px;
  width: 100%;
  padding: 8px 16px;
  border: none;
  background-color: white;
  font-size: 18px;
}
.form label input:focus,
.form label select:focus {
  outline: 0;
}

.buttonContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 32px;
  gap: 8px;
  width: 100%;
}

#title, #deadline, #startDate, #type, #desc {
  box-sizing: border-box;
  outline: 0;
  background-color: rgba(255, 255, 255, 0.6);
  font-size: 15px;
}

#desc {
  width: 100%;
  height: 80px;
  border: none;
  font-size: 15px;
  padding-left: 16px;
  margin-top: 16px;
}
#desc:focus {
  outline: 0;
}/*# sourceMappingURL=modal.css.map */